.section-pricing {
    @include mobile {
        width: 100%;
        height: auto;
        margin: 30px auto;
    }
    @include desktop {
        width: 1266px;
        height: 616px;
        margin: 100px auto 179px;
    }
    .container {
        .section-pricing__offer {
            .section-pricing__offer--title {
                font-family: var(--font2);
                font-size: 42px;
                font-weight: 400;
                text-align: center;
                margin-top: 0;
                margin-bottom: 25px;
            }
            .section-pricing__offer--content {
                display: flex;
                justify-content: space-between;
                align-items: center;
                @include mobile {
                    width: 100%;
                    height: auto;
                    flex-direction: column;
                }
                @include desktop {
                    width: 1266px;
                    height: 550px;
                }
                .section-pricing__offer-basic {
                    width: 372px;
                    border: 1px solid var(--color3);
                    border-radius: 18px;
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                    position: relative;
                    @include mobile {
                        height: auto;
                        margin-bottom: 50px;
                    }
                    @include desktop {
                        height: 461px;
                        flex-direction: column;
                    }
                    .section-pricing__offer-basic--top {
                        .section-pricing__offer-basic--title {
                            text-align: center;
                            margin-top: 0;
                            margin-bottom: 0;
                            font-family: var(--font2);
                            font-size: 24px;
                            font-weight: 700;
                            color: var(--color7);
                            @include desktop {
                                margin-top: 34px;
                            }
                        }
                        .section-pricing__offer-basic--price {
                            text-align: center;
                            margin-top: 14px;
                            font-family: var(--font1);
                            font-size: 46px;
                            font-weight: 300;
                            color: var(--color3);
                        }
                    }
                    .section-pricing__offer-basic--list {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        list-style: none;
                        margin-bottom: 40px;
                        @include desktop {
                            margin-bottom: 80px;
                        }
                        li {
                            &:nth-child(4) {
                                list-style-image: url(../assets/close.svg);
                            }
                            &:nth-child(5) {
                                list-style-image: url(../assets/close.svg);
                            }
                        }
                        .section-pricing__offer-basic--item {
                            text-align: center;
                            font-family: var(--font1);
                            color: #000;
                            font-size: 14px;
                            font-weight: 400;
                            list-style-image: url(../assets/ckechmark-1.svg);
                            line-height: 39px;
                        }
                    }
                    .section-pricing__offer-basic--confirm {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 150px;
                        height: 50px;
                        position: absolute;
                        bottom: -25px;
                        border: none;
                        background: #eb476e;
                        border-radius: 23px;
                        font-family: var(--font1);
                        font-weight: 700;
                        color: #fff;
                        text-decoration: none;
                    }
                }
                .section-pricing__offer-professional {
                    width: 405px;
                    border: 1px solid var(--color5);
                    border-radius: 18px;
                    box-shadow: 5px 10px 27px var(--shadow2);
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                    position: relative;
                    @include mobile {
                        height: auto;
                        margin-bottom: 50px;
                    }
                    @include desktop {
                        height: 500px;
                        flex-direction: column;
                    }
                    .section-pricing__offer-profesional--top {
                        .section-pricing__offer-professional--title {
                            text-align: center;
                            margin-top: 0;
                            margin-bottom: 0;
                            font-family: var(--font2);
                            font-size: 25px;
                            font-weight: 700;
                            color: var(--color7);
                            @include desktop {
                                margin-top: 34px;
                            }
                        }
                        .section-pricing__offer-professional--price {
                            text-align: center;
                            margin-top: 14px;
                            margin-bottom: 0;
                            font-family: var(--font1);
                            font-size: 46px;
                            font-weight: 300;
                            color: var(--color5);
                        }
                        .section-pricing__offer-professional--note {
                            font-family: var(--font2);
                            font-size: 12px;
                            color: var(--color7);
                            font-weight: 700;
                            text-align: center;
                            margin-top: 5px;
                        }
                    }
                    .section-pricing__offer-professional--list {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        list-style: none;
                        margin-bottom: 40px;
                        @include desktop {
                            margin-bottom: 80px;
                        }
                        li {
                            &:nth-child(5) {
                                list-style-image: url(../assets/close.svg);
                            }
                        }
                        .section-pricing__offer-professional--item {
                            text-align: center;
                            font-family: var(--font1);
                            color: #000;
                            font-size: 14px;
                            font-weight: 400;
                            list-style-image: url(../assets/ckechmark-1.svg);
                            line-height: 39px;
                            line-height: 45px;
                        }
                    }
                    .section-pricing__offer-professional--confirm {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 165px;
                        height: 50px;
                        position: absolute;
                        bottom: -25px;
                        border: none;
                        background: var(--color5);
                        box-shadow: 5px 10px 14px var(--shadow2);
                        border-radius: 23px;
                        font-family: var(--font1);
                        font-weight: 700;
                        color: #fff;
                        text-decoration: none;
                    }
                }
                .section-pricing__offer-premium {
                    width: 372px;
                    height: 461px;
                    border: 1px solid #748a83;
                    border-radius: 18px;
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                    position: relative;
                    @include mobile {
                        height: auto;
                        margin-bottom: 60px;
                    }
                    @include desktop {
                        height: 461px;
                        flex-direction: column;
                    }
                    .section-pricing__offer-premium--top {
                        .section-pricing__offer-premium--title {
                            text-align: center;
                            margin-top: 0;
                            margin-bottom: 0;
                            font-family: var(--font2);
                            font-size: 24px;
                            font-weight: 700;
                            color: var(--color7);
                            @include desktop {
                                margin-top: 34px;
                            }
                        }
                        .section-pricing__offer-premium--price {
                            text-align: center;
                            margin-top: 14px;
                            margin-bottom: 0;
                            font-family: var(--font1);
                            font-size: 46px;
                            font-weight: 300;
                            color: var(--color4);
                        }
                        .section-pricing__offer-premium--note {
                            font-family: var(--font2);
                            font-size: 12px;
                            color: var(--color7);
                            font-weight: 700;
                            text-align: center;
                            margin-top: 5px;
                        }
                    }
                    .section-pricing__offer-premium--list {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        list-style: none;
                        margin-bottom: 40px;
                        @include desktop {
                            margin-bottom: 80px;
                        }
                        .section-pricing__offer-premium--item {
                            text-align: center;
                            font-family: var(--font1);
                            color: #000;
                            font-size: 14px;
                            font-weight: 400;
                            list-style-image: url(../assets/ckechmark-1.svg);
                            line-height: 39px;
                        }
                    }
                    .section-pricing__offer-premium--confirm {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 150px;
                        height: 50px;
                        position: absolute;
                        bottom: -25px;
                        border: none;
                        background: var(--color4);
                        border-radius: 23px;
                        font-family: var(--font1);
                        font-weight: 700;
                        color: #fff;
                        text-decoration: none;
                    }
                }
            }
        }
    }
}